<template>
  <div class="info-wrapper">
    <div class="header">
      <span class="title">优惠码：AK728</span>
      <span class="button">取消优惠</span>
    </div>
    <van-tabs
      v-model="active"
      title-active-color="#7c322f"
      color="#7c322f"
      line-width="70"
      line-height="2"
      title-inactive-color="#666666"
      swipeable
      class="tabs"
    >
      <van-tab title="未支付订单">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="cell" v-for="item in list" :key="item">
            <div class="right">
              <van-image round width="50" height="50" src="https://img.yzcdn.cn/vant/cat.jpeg" />
              <div class="content">
                <div class="title">
                  <span class="bold">刘美丽</span>
                </div>
                <div class="label">
                  <span>订单编号：2020-09-15 8783 2738</span>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-tab>
      <van-tab title="已确认订单"></van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      active: 0,
      list: [],
      loading: false,
      finished: false,
    };
  },
  watch: {},
  computed: {},
  methods: {
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.info-wrapper {
  min-height: 100vh;
  background-color: #fff;
  .header {
    height: 50px;
    line-height: 50px;
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
    border-bottom: 1px solid #dbdbdb;
    .title {
      font-size: 16px;
    }
    .button {
      font-size: 14px;
      color: #7E3330;
    }
  }
  /deep/ .tabs {
    width: 100%;
    height: auto;
    pointer-events: none;
    .van-tabs__wrap {
      pointer-events: all;
      height: 40px;
    }
    .van-tabs__wrap::after {
      border-width: 0;
    }
    .van-tabs__content {
      padding: 0 15px;
      pointer-events: all;
    }
  }
  .cell {
    position: relative;
    display: flex;
    padding: 15px 0;
    border-bottom: 1px solid #dbdbdb;
    .bold {
      color: #333333;
      font-size: 16px;
    }
    .right {
      flex: 1;
      display: flex;
      align-items: center;
      font-size: 10px;
      .content {
        padding: 2px 0;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 8px;
        .title {
          color: #999999;
        }
        .label {
          color: #666666;
        }
      }
    }
    .left {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-size: 10px;
      .sum {
        font-size: 0;
        > span {
          font-size: 10px;
        }
        .bold {
          color: #333333;
          font-size: 16px;
        }
      }
      .button {
        padding: 4px 5px;
        color: #fff;
        background: linear-gradient(90deg, #c8a060, #e6c46a);
        border-radius: 5px;
      }
    }
  }
}
</style>


